        </textarea>
      </WzhMarkdown>
      <p><button @click="check"></button></p>
      <p class="result-tip">{{checkResult}}</p>
    </Group>
    <Group title="">
      <WzhMarkdown>
        <textarea>
Notification  requestPermission()  promise 

##### :
```js
