  if(permission === 'granted'){
    console.log('');
  }else if(permission === 'denied'){
    console.log('');
  }
});
```
        </textarea>
      </WzhMarkdown>
      <p><button @click="request"></button></p>
      <p class="result-tip">{{requestResult}}</p>
    </Group>
    <Group title="">
      <WzhMarkdown>
        <textarea>
`const notification = new Notification(title, options)`

- title
- options
  - body
  - tagtag
  - iconURL
  - imageURL
  - data
  - requireInteractionfalse

