    body: '',
    tag: 'shop',
    icon: require('../../assets/logo.png'),
    requireInteraction: true
})
```
        </textarea>
      </WzhMarkdown>
      <p><button @click="send"></button></p>
    </Group>
    <Group title="">
      <WzhMarkdown>
        <textarea>
