    window.open(n.data.url, '_blank');      // 
    n.close();                              // 
}
```
        </textarea>
      </WzhMarkdown>
      <p><button @click="sendAndAddEvent"></button></p>
    </Group>
  </div>
</template>

<script>
const icon = require('../../assets/logo.png')

export default {
  name: 'Notification',
  data () {
    return {
      checkResult: '',
      requestResult: ''
    }
  },
  components: {
  },
  methods: {
    check () {
      if(Notification.permission === 'granted'){
          this.checkResult = ''
      }else if(Notification.permission === 'denied'){
          this.checkResult = ''
      }else{
          this.checkResult = ''
      }
    },
    request () {
      Notification.requestPermission().then((permission) => {
        if(permission === 'granted'){
            this.requestResult = ''
        }else if(permission === 'denied'){
            this.requestResult = 'i '
        }
      })
    },
    send () {
      new Notification('',{
        body: '',
        icon,
      })
    },
    sendAndShut () {
      const n = new Notification('',{
        body: '',
        icon,
      })
      setTimeout(function() {
        n.close()
      }, 3000)
    },
    sendAndAddEvent () {
      const n = new Notification('',{
          body: '',
          icon,
          data: {
            url: 'http://www.google.com'
          }
      })
      n.onclick = function(){
        window.open(n.data.url, '_blank')      // 
        n.close()                              // 
      }
    }
  }
}
</script>

