<template>
  <div id="Lifecycle">
    <h4></h4>
    <p>prop<button @click="updateProp"></button><span>{{count}}</span></p>
    <p>data<button @click="updateData"></button><span>{{selfCount}}</span></p>
  </div>
</template>

<script>
export default {
  name: 'Lifecycle',
  props: {
    count: Number
  },
  data () {
    return {
      selfCount: 0
    }
  },
  beforeCreate () {
    console.log('I am in beforeCreate')
  },
  created () {
    console.log('I am in created')
  },
  beforeMount () {
    console.log('I am in beforeMount')
  },
  mounted () {
    console.log('I am in mounted')
  },
  beforeUpdate () {
    console.log('I am in beforeUpdate')
  },
  updated () {
    console.log('I am in updated')
  },
  beforeDestroy () {
    console.log('I am in beforeDestroy')
  },
  destroyed () {
    console.log('I am in destroyed')
  },
  methods: {
    updateProp () {
      this.$emit('addCount')
    },
    updateData () {
      this.selfCount += 1
    }
  }
}
</script>

