##### :
```js
import ComponentA from './ComponentA'
import ComponentB from './ComponentB'

export default {
  name: 'DynamicComponent',
  data () {
    return {
      component: ComponentA
    }
  },
  methods: {
    changeComponent (key) {
      if (key === 'A') {
        this.component = ComponentA
      } else {
        this.component = ComponentB
      }
    }
  }
}
